* {
  box-sizing: border-box !important;
}

html, body {
  width: 100%;
  height: 100%;
  background-color: #222;
  margin: 0%;
}

body {
  color: #fff;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

#controls {
  border-right: 1px solid #fff;
  padding: 1rem;
}

#controls input {
  width: 100%;
  display: block;
}

#controls button {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #1d5494;
  border: none;
  margin-bottom: 1rem;
  padding: 1rem;
  font-size: 1.25rem;
}

#controls button:hover {
  background-color: #236abb;
}

#controls button:active {
  background-color: #0e4b91;
}

